.leaflet-popup {
--light: 0.95;
--dark: 0.1;
--width: 5px;
// --background: #cbcbcb;
--background: #e8e8e8;

  .leaflet-popup-content-wrapper {
    border-radius: 0;
    padding: 0;
    pointer-events: none;

        background-color: var(--background);
    .leaflet-popup-content {

      position: relative;
      margin: 0;
      height: 55px;
      display: flex;
      justify-content: center;

      div {
        position: absolute;

        &:nth-child(1) {
          height: var(--width);
          width: 100%;
          background-color: rgba(255, 255, 255, var(--light));
          box-shadow: 0 0 20px 5px rgba(255, 255, 255, 0.33);
        }

        &:nth-child(2) {
          width: var(--width);
          height: 100%;
          background-color: rgba(0, 0, 0, var(--dark));
          top: 0;
          left: calc(100% - var(--width));
        }

        &:nth-child(3) {
          width: var(--width);
          height: 100%;
          background-color: rgba(255, 255, 255, var(--light));
          left: 0;
          box-shadow: 0 0 20px 5px rgba(255, 255, 255, 0.33);
        }

        &:nth-child(4) {
          height: var(--width);
          width: 100%;
          background-color: rgba(0, 0, 0, var(--dark));
          top: calc(100% - var(--width));
        }
      }

      p {
        // padding: 20px;
        padding: 5px 25px 0;
        margin: 0;
        align-self: center;
      }
    }
  }

  .leaflet-popup-tip {
      background-color: var(--background);
      box-shadow: 0 0 0 50px rgba(0,0,0,calc(var(--dark) * 1.5)) inset;

  }
}
