@font-face {
  font-family: MCFFR;
  src: url(../fonts/mcffr.woff) format("woff");
}

@font-face {
  font-family: minecraftia;
  src: url(../fonts/minecraftia.woff) format("woff");
}

* {
  margin: 0;
  padding: 0;
  font-family: minecraftia;
  font-weight: lighter;
  scrollbar-width: thin;
  scrollbar-color: rgba(0, 0, 0, 0.125) transparent;
}

main {
  background-color: #143055;
  height: calc(100vh - 80px - 7px);
  z-index: 1;
  position: relative;
}

.border {
  position: relative;

  div {
    position: absolute;
    --light: 0.5;
    --dark: 0.2;
    --width: 5px;

    &:nth-child(1) {
      height: var(--width);
      width: 100%;
      background-color: rgba(255, 255, 255, var(--light));
      box-shadow: 0 0 20px 3px rgba(255, 255, 255, 0.33);
    }

    &:nth-child(2) {
      width: var(--width);
      height: 100%;
      background-color: rgba(0, 0, 0, var(--dark));
      top: 0;
      left: calc(100% - var(--width));
    }

    &:nth-child(3) {
      width: var(--width);
      height: 100%;
      background-color: rgba(255, 255, 255, var(--light));
      box-shadow: 0 0 20px 3px rgba(255, 255, 255, 0.33);
    }

    &:nth-child(4) {
      height: var(--width);
      width: 100%;
      background-color: rgba(0, 0, 0, var(--dark));
      top: calc(100% - var(--width));
    }
  }
}

::-webkit-scrollbar {
  width: 7px;
}

::-webkit-scrollbar-track {
  background: transparent;
}

::-webkit-scrollbar-thumb {
  background: rgba(0, 0, 0, 0.125);
}

::-webkit-scrollbar-thumb:hover {
  background: rgba(0, 0, 0, 0.25);
}
