button {
  padding: 10px;
  border: none;
  background: none;
  background-color: #cbcbcb;
  font-size: 20px;
  position: relative;
  // box-shadow: 0 0 10px 5px rgba(255,255,255,0.1);

  &:before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    box-shadow: 0 0 10px 5px rgba(255, 255, 255, 0.1);
  }


  div {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;

    .a > div {
      position: absolute;
      --light: 0.5;
      --dark: 0.2;
      --width: 5px;

      &.top {
        height: var(--width);
        width: 100%;
        background-color: rgba(255, 255, 255, var(--light));
      }

      &.right {
        width: var(--width);
        height: 100%;
        background-color: rgba(0, 0, 0, var(--dark));
        top: 0;
        left: calc(100% - var(--width));
      }

      &.left {
        width: var(--width);
        height: 100%;
        background-color: rgba(255, 255, 255, var(--light));
      }

      &.bottom {
        height: var(--width);
        width: 100%;
        background-color: rgba(0, 0, 0, var(--dark));
        top: calc(100% - var(--width));
      }
    }
  }
}

.active {
  button {
    background-color: #70b35b;
    box-shadow: 5px 5px 0 0 rgba(0 ,0,0,0.25);

    p {

        color: #fff;
    }

  }
}
