#root > header {
  height: 80px;
  display: flex;
  justify-content: space-between;

  background: #FFFFFF;
  background: linear-gradient(to bottom right, #FFFFFF, #4E5E75);

  box-shadow: 0 0 20px rgba(0, 0, 0, 0.5);
  border-bottom: 5px solid #b1b7c3;
  outline: 5px solid rgba(0, 0, 0, 0.25);
  position: relative;
  z-index: 2;
  padding: 0 20px;

  > * {
    display: flex;
    align-self: center;
    z-index: 1;
  }

  > div {
    margin-top: 10px;

    h1 {
      font-size: 21px;
      color: #454543;
      filter: drop-shadow(0 4px 0 rgba(0, 0, 0, 0.1));
      font-family: MCFFR;
    }

    img {
      height: 85px;
      margin: 0 20px;
    }

    * {
      align-self: center;
    }
  }

  ul {
    list-style: none;

    img {
      height: 40px;
    }

    li {
      margin-left: 15px;
    }

    a {
      height: 60px;
      display: flex;
    }
  }
}
