#aside_settings {
  padding-top: 32px;

  table {
    width: 100%;
  }

  td {
    width: 50%;
    font-size: 12px;
    line-height: 25px;
    padding-bottom: 25px;
  }

  td:nth-child(2) {
    width: 50%;
  }

  button {
    width: 100%;
    height: 50px;
    color: white;
    font-family: MCFFR;
    padding: 5px;

    &.active {
      background-color: #70b35b;
    }

    > div {
      pointer-events: none;
    }
  }

  .hidor > div {
    justify-content: space-between;
    align-items: center;
  }

  .title {
    text-transform: capitalize;
    font-size: 16px;
    opacity: 0.75;
    position: relative;
    display: inline-block;
    margin: 15px 0 5px;

    &:after {
      content: "";
      height: 5px;
      width: 1000px;
      left: calc(100% + 20px);
      top: 33%;
      position: absolute;
      background-color: rgba(255, 255, 255, 0.25);
    }
  }
}
