.leaflet-marker-icon {
  border-radius: 50%;
  outline: 2px #FFF solid !important;
  outline-offset: -2px;
  box-shadow: 0 0 0px 2px rgba(0, 0, 0, 0.25);
  // filter: drop-shadow(0 0 10px rgba(0, 0, 0, 10.25));
  filter: brightness(1.2);
}

.leaflet-tooltip {
  animation: appear .25s ease;
  top: -10px;
  border-radius: 0;
    opacity: .75 !important;
  p {
    padding: 7px 5px 0 5px;
  }
}

@keyframes appear {
  0% {
    top: -6.9px;
    opacity: 0;
  }

  50% {
    opacity: 1;
  }

  100% {
    top: -10px;
  }
}
