aside {
  background-color: #153153f2;
  user-select: none;
  width: 375px;
  height: calc(100% - 80px - 20px);
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.5);
  outline: 5px solid rgba(0, 0, 0, 0.25);
  position: relative;
  z-index: 1;
  position: fixed;
  backdrop-filter: blur(17px);
  border-right: 5px solid rgba(255, 255, 255, 0.1);
  // padding-right: 5px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding-bottom: 20px;
  overflow: hidden;

  content {
    height: calc(100% - 87px);
    display: flex;
    width: calc(375px * 5);
    transition: transform 0.5s;

    #search_div {
      margin-top: 32px;

      input {
        width: calc(100% - 18px);
      }

      div {
        // --light: 0.2;
        // --dark: 0.1;
        // background-color: rgba(255,255,255,.2);
        // box-shadow: none;
      }
    }
  }

  content > div {
    // height: calc(100% - 87px);
    height: calc(100% - 0px);
    width: 100%;
    // &:before {
    //   content: "";
    //   width: 100%;
    //   box-shadow: 0 0 20px 0 rgba(0,0,0,.25);
    //   height: 0;
    // }

    h1 {
      position: absolute;
      margin: 0 0 0px 25px;
      filter: drop-shadow(0 0 10px rgba(0, 0, 0, 1));
      font-weight: 600;
      bottom: 0;
      font-size: 24px;
      white-space: nowrap;
      width: calc(100% - 50px);
      overflow: hidden;
      text-overflow: ellipsis;
      user-select: text;
    }

    #profile_img_div.border {
      div {
        --light: 0.2;
      }
      > div {
        box-shadow: none !important;
      }
    }

    #profile_img_div {
      overflow: hidden;
      &::after {
        content: "";
        position: absolute;
        height: 10px;
        width: 100%;
        left: 0;
        top: -10px;
        box-shadow: 0 5px 75px 20px rgba(0, 0, 0, 0.75);
        z-index: 95;
      }

      #profile_img {
        // margin-top: 5px;
        margin: 5px;
        width: calc(100% - 10px);
        height: 150px;
        background-position: center;
        background-size: cover;
        position: relative;
      }
    }

    .hidor {
      // overflow-x: hidden;
      margin: 0 25px;
      // height: 100%;
      overflow: hidden;
    }

    #content {
      overflow-y: auto;
      width: calc(100%);
      overflow-y: auto;
      height: calc(100% - 180px);

      .title {
        text-transform: capitalize;
        font-size: 16px;
        opacity: 0.75;
        position: relative;
        display: inline-block;
        margin: 15px 0 5px;

        &:after {
          content: "";
          height: 5px;
          width: 1000px;
          left: calc(100% + 20px);
          top: 33%;
          position: absolute;
          background-color: rgba(255, 255, 255, 0.25);
        }
      }

      .journal {
        user-select: text;
        position: relative;
        image-rendering: pixelated;
        padding: 20px;
        color: rgba(0, 0, 0, 0.66);
        line-height: 20px;
        background-image: url("https://media.discordapp.net/attachments/1003060853253357658/1005510136720265278/book-content.png");
        background-repeat: no-repeat;
        background-size: cover;
        font-size: 16px;

        &:before {
          content: "";
          position: absolute;
          top: 0;
          left: 0;
          height: 68px;
          width: 100%;
          background-image: url("https://media.discordapp.net/attachments/1003060853253357658/1005515260251340861/book-top.png");
          background-repeat: no-repeat;
          background-size: cover;
          z-index: 1;
        }

        &:after {
          content: "";
          position: absolute;
          bottom: 0;
          left: 0;
          height: 34px;
          width: 100%;
          background-image: url("https://media.discordapp.net/attachments/1003060853253357658/1005510137106153672/book-bottom.png");
          background-repeat: no-repeat;
          background-size: cover;
          z-index: 2;
        }

        > p {
          z-index: 3;
          position: relative;
          overflow-y: auto;
          scrollbar-width: thin;
          scrollbar-color: rgba(0, 0, 0, 0.25) transparent;
          max-height: 200px;
          padding: 5px;
          padding-left: 20px;
        }

        .brand {
          background-image: url("https://cdn.discordapp.com/attachments/1003060853253357658/1005516359368716420/book-c.png");
          background-repeat: no-repeat;
          background-size: cover;
          z-index: 3;
          position: absolute;
          top: 0;
          left: 13px;
          height: 62px;
          width: 14px;
          filter: drop-shadow(4px 5px 0px rgba(0, 0, 0, 0.25));
        }

        table {
          position: relative;
          z-index: 3;
          font-size: 12px;
          width: 100%;

          tr {
            // height: 30px;
            margin: 5px 0;

            td {
              padding: 15px 0 5px 10px;

              p {
                width: 75%;
              }
            }

            td:first-child {
              background-color: #dccba9;

              font-size: 12px;
            }

            td:last-child {
              p {
                border-bottom: 4px solid #dccba9;
              }
            }
          }
        }

        ul {
          z-index: 3;
          position: relative;
          display: flex;
          flex-wrap: wrap;
          list-style: none;

          li {
            height: 82px;
            width: 82px;
            background-color: #dccba9;
            margin: 5px;
            display: flex;
            flex-direction: column;
            text-align: center;
            position: relative;

            p {
              font-size: 13px;
              width: 100%;
              padding: 15px 0 5px 0;
              line-height: 0;
              margin-top: 4px;

              span {
                height: 50%;
              }
              // white-space: nowrap;
            }

            img {
              width: 45px;
              margin-top: 4px;
              margin-right: auto;
              margin-left: auto;
            }
          }
        }
      }

      > div > p {
        margin-top: 32px;
        opacity: .75;
        font-size: 12px;
      }
    }
  }

  header {
    ul {
      display: flex;
      list-style: none;
      padding: 5px 10px;
      justify-content: space-evenly;

      li {
        color: #8b96a8;
        padding: 7px 20px 2px;

        &.active {
          background-color: rgba(0, 0, 0, 0.75);
        }

        > * {
          pointer-events: none;
        }
      }
    }

    > div {
      margin: 0 32px;
      text-align: center;
      overflow: hidden;

      h2 {
        color: rgba(255, 255, 255, 0.75);
        font-size: 16px;
        display: inline-flex;
        position: relative;

        &:before {
          content: "";
          height: 5px;
          width: 150%;
          left: calc(100% + 20px);
          top: 33%;
          position: absolute;
          background-color: rgba(255, 255, 255, 0.25);
        }

        &:after {
          content: "";
          height: 5px;
          width: 150%;
          left: -20px;
          top: 33%;
          position: absolute;
          background-color: rgba(255, 255, 255, 0.25);
          transform: rotateY(180deg);
          transform-origin: left;
        }
      }
    }
  }

  #search_list {
    height: calc(100% - 125px);
    overflow: auto;
    list-style: none;
    margin-top: 20px;

    li {
      font-size: 16px;
      padding: 10px 0 0px 10px;
      // background-color: red;
      margin: 10px 0 10px 0;
      width: 100%;
      background-repeat: no-repeat;
      background-size: cover;
      background-position: center;
      background-color: rgba(255, 255, 255, 0.125);

      &:hover {
        background-color: rgba(255, 255, 255, 0.25);
        filter: brightness(1.2);
      }
    }
  }

  > content:nth-child(1) > div:nth-child(3) > div:nth-child(1) {
    height: 100%;
    margin-top: 32px;
    height: calc(100% - 64px);

    > div {
      box-shadow: none;
      --light: 0.33;
    }

    textarea {
      font-size: 8px;
      height: calc(100% - 30px);
      width: calc(100% - 30px);
      border: 10px solid rgba(255, 255, 255, 0.25);


        &:focus,
        &:active {
        background-color: rgba(0,0,0,.25);
          outline: none;
        }
    }
  }
}
